@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

.main {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  background-color: rgb(161, 210, 255);
}

.weather-card {
  display: flex;
  justify-content: space-between;
}

.app-header {
  font-family: Poppins;
  margin: 20px;
  font-size: 35px;
  color: #2196f3;
  font-weight: 600;
}

.weather-card-child {
  font-family: Poppins !important;
  margin-top: 10px;
}

.weather-card-main {
  width: 400px !important;
  border-top: 2px solid red !important;
}

.forecast-card {
  border-top: 2px solid #ffc107 !important;
}

.icon-container {
  position: relative;
  right: -80px;
}

.temp-desc {
  text-transform: capitalize;
  font-family: Poppins;
}

.forecast-header {
  font-family: Poppins !important;
}

.forecast-main-header {
  font-family: Poppins;
  margin: 20px;
  font-size: 26px;
  color: #2196f3;
  font-weight: 600;
  text-align: center;
}

.forecast-date {
  font-family: Poppins !important;
  font-size: 15px !important;
  margin-bottom: 20px;
}

@media screen and (max-width: 376px) {
  .icon-container {
    position: relative;
    right: -30px;
  }
}

@media screen and (max-width: 321px) {
  .app-header {
    font-size: 20px;
  }
}
